import { PageContainer } from '@ant-design/pro-components';
import { useModel } from '@umijs/max';
import { Card, theme } from 'antd';
import React from 'react';
import Settings from '@/../config/defaultSettings';

/**
 * Card Component to display title, description, and a link for more information
 * @param title - Title to display in the card
 * @param index - Index for displaying the card number
 * @param desc - Description text inside the card
 * @param href - URL link to navigate for more information
 * @returns JSX Element
 */
const InfoCard: React.FC<{
  title: string;
  index: number;
  desc: string;
  href: string;
}> = ({ title, href, index, desc }) => {
  const { useToken } = theme;
  const { token } = useToken();

  return (
    <div
      style={{
        backgroundColor: token.colorBgContainer,
        boxShadow: token.boxShadow,
        borderRadius: '8px',
        fontSize: '14px',
        color: token.colorTextSecondary,
        lineHeight: '22px',
        padding: '16px 19px',
        minWidth: '220px',
        flex: 1,
      }}
    >
      <div style={{ display: 'flex', gap: '4px', alignItems: 'center' }}>
        <div
          style={{
            width: 48,
            height: 48,
            lineHeight: '22px',
            backgroundSize: '100%',
            textAlign: 'center',
            padding: '8px 16px 16px 12px',
            color: '#FFF',
            fontWeight: 'bold',
            backgroundImage:
              "url('https://gw.alipayobjects.com/zos/bmw-prod/daaf8d50-8e6d-4251-905d-676a24ddfa12.svg')",
          }}
        >
          {index}
        </div>
        <div
          style={{
            fontSize: '16px',
            color: token.colorText,
            paddingBottom: 8,
          }}
        >
          {title}
        </div>
      </div>
      <div
        style={{
          fontSize: '14px',
          color: token.colorTextSecondary,
          textAlign: 'justify',
          lineHeight: '22px',
          marginBottom: 8,
        }}
      >
        {desc}
      </div>
      <a href={href} target="_blank" rel="noreferrer">
        Get started {'>'}
      </a>
    </div>
  );
};

/**
 * Welcome Page displaying a greeting message and a few cards with more information
 * @returns JSX Element
 */
const Welcome: React.FC = () => {
  const { token } = theme.useToken();
  const { initialState } = useModel('@@initialState');

  const getCardBackground = () =>
    initialState?.settings?.navTheme === 'realDark'
      ? 'linear-gradient(75deg, #1A1B1F 0%, #191C1F 100%)'
      : 'linear-gradient(75deg, #FBFDFF 0%, #F5F7FF 100%)';

  return (
    <PageContainer>
      <Card
        style={{
          borderRadius: 8,
        }}
        bodyStyle={{
          backgroundImage: getCardBackground(),
        }}
      >
        <div
          style={{
            backgroundPosition: '100% -30%',
            backgroundRepeat: 'no-repeat',
            backgroundSize: '274px auto',
            backgroundImage:
              "url('https://gw.alipayobjects.com/mdn/rms_a9745b/afts/img/A*BuFmQqsB2iAAAAAAAAAAAAAAARQnAQ')",
          }}
        >
          <div
            style={{
              fontSize: '20px',
              color: token.colorTextHeading,
            }}
          >
            {`Welcome to ${Settings.title}`}
          </div>
          <p
            style={{
              fontSize: '14px',
              color: token.colorTextSecondary,
              lineHeight: '22px',
              marginTop: 16,
              marginBottom: 32,
              width: '65%',
            }}
          >
            Effortlessly check-in to your destination with our quick and easy system. Get started in
            just a few clicks!
          </p>
          <div style={{ display: 'flex', flexWrap: 'wrap', gap: 16 }}>
            <InfoCard
              index={1}
              href="/events"
              title="Events"
              desc="Easily organize and manage events with customizable options and scheduling tools."
            />
            <InfoCard
              index={2}
              title="Rooms"
              href="/rooms"
              desc="Set up and manage rooms effortlessly for meetings, discussions, or private gatherings."
            />
          </div>
        </div>
      </Card>
    </PageContainer>
  );
};

export default Welcome;
